<template>
  <div id="news-manage">
    <transition name="funcList">
      <div v-show="!uploadStatus" class="func-operation">
        <div class="news-item head pointer" @click="upload">
          <p>新闻上传</p>
        </div>
        <div class="newsList">
          <div class="news-item head pointer" @click="show(0)">
            <p class="left">首页新闻</p>
            <div v-show="showCarousel" class="back pointer left"></div>
          </div>
          <transition name="operation">
            <div v-show="showCarousel" class="carousel-wrapper">
              <div v-for="(item, index) in carouselList" v-bind:key="index" class="news-item">
                <p class="pointer" @click="read(index)">{{item.title}}</p>
                <div class="button delete right" @click="deleteItem(0, index)">
                  <p>删除</p>
                </div>
                <div class="button hide right" @click="carouselize(0, index)">
                  <p>隐藏</p>
                </div>
                <div class="button setting right" @click="downItem(index)">
                  <p>下移</p>
                </div>
                <div class="button setting right" @click="upItem(index)">
                  <p>上移</p>
                </div>
              </div>
            </div>
          </transition>
        </div>
        <div class="newsList">
          <div class="news-item head pointer" @click="show(1)">
            <p class="left">全部新闻</p>
            <div v-show="showAllNews" class="back pointer left"></div>
          </div>
          <transition name="operation">
            <div v-show="showAllNews" class="carousel-wrapper">
              <div v-for="(item, index) in newsList" v-bind:key="index" class="news-item">
                <p class="pointer" @click="read(index)">{{item.title}}</p>
                <div class="button delete right" @click="deleteItem(1, index)">
                  <p>删除</p>
                </div>
                <div class="button setting right" @click="editItem(1, index)">
                  <p>编辑</p>
                </div>
                <div class="button show right" @click="carouselize(1, index)">
                  <p>置顶</p>
                </div>
              </div>
            </div>
          </transition>
        </div>
      </div>
    </transition>
    <router-view v-show="uploadStatus"></router-view>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name: 'news-manage',
  data () {
    return {
      uploadStatus: true,
      showCarousel: false,
      showAllNews: false,
      carouselList: [],
      newsList: []
    }
  },
  methods: {
    upload: function () {
      this.$router.push({path: '/user/news/upload'})
    },
    show: function (i) {
      if (i === 0) {
        this.showCarousel = !this.showCarousel
      } else {
        this.showAllNews = !this.showAllNews
      }
      var self = this
      setTimeout(function () {
        var h
        var temp = $('#user').height() - window.innerHeight
        if (!self.$root.adapt) {
          if (i === 0) {
            h = $('#main').height() + $('.func-item').eq(0).height() + 17 + $('.news-item').eq(0).height()
          } else {
            h = $('#main').height() + $('.func-item').eq(0).height() + 17 + $('.news-item').eq(0).height() + 17 + $('.newsList').eq(0).height()
          }
        } else {
          if (i === 0) {
            h = $('#main').height() + $('.main-function').eq(0).height() + $('.news-item').eq(0).height() + 20
          } else {
            // eslint-disable-next-line no-redeclare
            h = $('#main').height() + $('.main-function').eq(0).height() + $('.news-item').eq(0).height() + 20 + $('.newsList').eq(0).height()
          }
        }
        if (temp > h) {
          temp = h
        }
        $('#app').animate({scrollTop: temp}, 300)
      }, 17)
    },
    read: function (i) {
      alert(i)
    },
    deleteItem: function (mode, i) {
      if (mode === 0) {
        this.carouselList.splice(i, 1)
      } else {
        this.newsList.splice(i, 1)
      }
    },
    carouselize: function (mode, i) {
      if (mode === 0) {
        this.newsList.unshift(this.carouselList[i])
        this.carouselList.splice(i, 1)
      } else if (this.carouselList.length < 6) {
        this.carouselList.push(this.newsList[i])
        this.newsList.splice(i, 1)
      }
    },
    downItem: function (i) {
      if (i < this.carouselList.length - 1) {
        var obj = this.carouselList[i]
        this.$set(this.carouselList, i, this.carouselList[i + 1])
        this.$set(this.carouselList, i + 1, obj)
      } else {
        var prompt = {
          text: '此新闻是末项！',
          duration: 1200
        }
        this.$root.eventHub.$emit('showPrompt', prompt)
      }
    },
    upItem: function (i) {
      if (i > 0) {
        var obj = this.carouselList[i]
        this.$set(this.carouselList, i, this.carouselList[i - 1])
        this.$set(this.carouselList, i - 1, obj)
      } else {
        var prompt = {
          text: '此新闻是首项！',
          duration: 1200
        }
        this.$root.eventHub.$emit('showPrompt', prompt)
      }
    },
    editItem: function (i) {
      alert(i)
    }
  },
  created () {
    var self = this
    $.ajax({
      async: true,
      url: '/carousel/',
      type: 'GET',
      data: {},
      success: function (data) {
        self.carouselList = data
        for (var i = 0; i < self.carouselList.length; i++) {
          self.carouselList[i].title = self.carouselList[i].title.replace(/≮/g, ' ')
        }
      }
    })
    $.ajax({
      async: true,
      url: '/news_list/',
      type: 'GET',
      data: {},
      success: function (data) {
        self.newsList = data
        for (var i = 0; i < self.newsList.length; i++) {
          self.newsList[i].title = self.newsList[i].title.replace(/≮/g, ' ')
        }
      }
    })
  },
  mounted () {
    var self = this
    self.uploadStatus = false
    this.$root.eventHub.$on('cancelUpload', function () {
      self.uploadStatus = false
    })
    this.$root.eventHub.$on('Upload', function () {
      self.uploadStatus = true
    })
  }
}
</script>

<style scoped>
  .func-operation{
    height: auto;
    position: relative;
    z-index: 3;
  }
  .news-item{
    overflow: hidden;
    background-color: white;
    border-bottom: 1px solid #f0f2f5;
    position: relative;
    z-index: 3;
  }
  .news-item>p{
    font-size: 18px;
    overflow: hidden;
  }
  .newsList{
    width: 100%;
    height: auto;
    overflow: hidden;
  }
  .carousel-wrapper{
    position: relative;
    z-index: 0;
  }
  .button{
    height: 30px;
    border-radius: 4px;
    margin: 5px 0 5px 16px;
    padding: 0 12px;
    cursor: pointer;
  }
  .button p{
    font-size: 14px;
    line-height: 28px;
    color: white;
  }
  .setting{
    background: #4169e1;
  }
  .delete{
    background: #dc143c;
  }
  .hide{
    background: #808080;
  }
  .show{
    background: #FF8C00;
  }
  .back{
    width: 40px;
    height: 40px;
    background: url("../../resource/assets/back2.png") 50% no-repeat;
    background-size: 50%;
  }
  .funcList-enter-active, .operation-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .funcList-enter, .operation-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  @media only screen and (max-device-width: 767px) {
    .func-operation{
      width: calc(100% - 40px);
      margin: 0 20px;
    }
    .news-item{
      width: 100%;
      padding: 6px 0 10px;
    }
    .news-item>p{
      line-height: 41px;
    }
    .news-item:not(.head)>p{
      line-height: 41px;
      padding-bottom: 6px;
      max-height: 35px;
      overflow: hidden;
    }
    .back{
      float: right;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .func-operation{
      width: calc(100% - 36px);
      margin: 0 18px;
    }
    .news-item{
      width: calc(100% - 40px);
      height: 39px;
      padding: 10px 20px;
    }
    .news-item>p{
      line-height: 39px;
    }
    .news-item:not(.head)>p{
      float: left;
    }
    .back{
      margin-left: 10px;
    }
    .newsList:nth-of-type(2) .news-item:not(.head)>p{
      max-width: calc(100% - 280px);
    }
    .newsList:nth-of-type(3) .news-item:not(.head)>p{
      max-width: calc(100% - 210px);
    }
  }
</style>



